import React from 'react';
import getUrlParam from '@/utils/get-url-params';
import { QUERY_INFORMATION_DETAIL } from '@/helpers/APIHelper';
import { useQuery } from '@apollo/client';
import { MsgDetailBox, MsgDetailContent, MsgDetailTitle } from './style';

const Detail: React.FC = () => {
  const { id } = getUrlParam();

  const { data = {} } = useQuery(QUERY_INFORMATION_DETAIL, {
    variables: {
      messageId: id,
    },
  });
  const { content, title } = data.getMessageById || {};

  return (
    <MsgDetailBox>
      <MsgDetailTitle>{title}</MsgDetailTitle>
      <MsgDetailContent>{content}</MsgDetailContent>
    </MsgDetailBox>
  );
};

export default Detail;
